header {
  width: 100%;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 50px;
  padding-bottom: 20px;
  background-color: transparent;
  transition: all 0.2s ease;
}

header .container {
  padding: 0 50px;
}

.header-nonactive .header-menu {
  opacity: 0;
}

.header-active {
  transition-delay: 0.4s;
  background-color: #fff;
}

.header-active .header-menu {
  opacity: 1;
  transition-delay: 0.4s;
  transition: all 0.2s ease;
}

.header-active .header-menu li {
  color: #000;
}

.header-active .header-menu a {
  color: #000;
  border-color: #000;
}

.header-active .header-menu .active {
  color: #2eccad;
  border-color: #2eccad;
