// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------
footer{
	width: 75%;
	height: 295px;
	bottom: 0;
	left: 50%;
	transform: translate(-50%, 100%);
	transition: all 0.5s ease;
	&.active{
		transform: translate(-50%, 0);
	}
}
.copyright{
	width: 100%;
	text-align: center;
	color: #a584b8;
	font-weight: 600;
	font-size: 12px;
	bottom: 40px;
}