// -----------------------------------------------------------------------------
// This file contains styles that are specific to the apply expert page.
// -----------------------------------------------------------------------------
.apply-container{
	background-position: center bottom;
	background-repeat: repeat-x;
	background-size: 700px;
	bottom: auto;
}
.apply-wrapper{
	width: 100%;
	max-width: 650px;
	margin: 0 auto;
	padding-bottom: 300px;
	overflow: hidden;
	a, button{
		display: block;
		width: 240px;
		margin: 50px auto 0;
		font-weight: 700;
		padding: 10px 0;
		text-align: center;
		span{
			font-size: 20px;
			font-weight: 400;
		}
		img, svg{
			padding-top: 2px;
			width: 20px;
			height: 15px;
			margin-left: 5px;
		}
	}
	.primary-button{
		svg{
			.st0{
				fill: #fff;
			}
		}
	}
	.disabled-button{
		svg{
			.st0{
				fill: #555;
			}
		}
	}
}
.apply-form_wrap{
	position: absolute;
	visibility: hidden;
	opacity: 0;
	transform: translateX(-100%);
	transition: all 0.8s ease;
	&.active{
		position: relative;
		visibility: visible;
		opacity: 1;
		transform: translateX(0);
	}
	&.inputed{
		position: absolute;
		visibility: hidden;
		opacity: 0;
		transform: translateX(-100%);
	}
	&.active + .active{
		position: relative;
		visibility: visible;
		opacity: 1;
		transform: translateX(0);
	}
}
.apply-form_wrap + .apply-form_wrap{
	transform: translateX(100%);
}
.apply-title{
	margin-bottom: 90px;
	.apply-bigtitle, .apply-smtitle{
		width: 100%;
		font-size: 36px;
		color: #793da0;
		font-weight: 600;
		letter-spacing: -2px;
		margin-bottom: 10px;
		font-family: einstellungbold;
	}
	.apply-smtitle{
		font-size: 24px;
	}
	.apply-bigtitle:before{
		content: "";
		position: absolute;
		bottom: -30px;
		left: 50%;
		transform: translateX(-50%);
		width: 100px;
		height: 2px;
		background-color: rgba(121, 61, 160, 0.25);
	}
	p{
		font-size: 14px;
		color: #7f7f7f;
		font-weight: 500;
		width: 100%;
		max-width: 500px;
		margin: 0 auto;
	}
}
.apply-form{
	margin-bottom: 30px;
	&.non-active{
		.apply-form_title{
			opacity: 0.3;
		}
	}
	p{
		font-size: 12px;
		color: #7f7f7f;
		font-weight: 400;
		font-family: einstellung;
	}
	.apply-error{
		margin-top: 5px;
		color: red;
		display: none;
	}
	span{
		color: #000;
		font-weight: 700;
	}
	input, textarea{
		padding: 15px 10px;
		border-color: #cccccc;
		&::placeholder{
			color: #cccccc;
		}
		&:focus, .filled{
			border-color: #8959a5;
			&::placeholder{
				color: #847f7f;
			}
		}
	}
}
.apply-form_textarea{
	padding: 15px 0;
	border-bottom: 1px solid #8959a5;
	border-color: #cccccc;
}
.apply-form_title{
	color: #793da0;
	font-size: 24px;
	margin-bottom: 20px;
	font-family: einstellungbold;
	span{
		color: #000000;
		text-decoration: underline;
	}
}
.apply-optional{
	font-size: 14px;
	margin: 10px 0 0;
}
.apply-option{
	ul{
		list-style: none;
		padding: 0;
	}
	li{
		padding: 5px;
	}
	input{
		visibility: hidden;
  		position: absolute;
  		display: none;
	}
	label{
		font-size: 14px;
		color: #000;
		font-weight: 700;
		width: 100%;
		display: inline-block;
		position: relative;
		border: 1px solid #cccccc;
		padding: 15px;
		padding-left: 42px;
	}
	input[type=checkbox] + label:before,
	input[type=radio] + label:before{
		content: " ";
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 15px;
		display: inline-block;
		vertical-align: middle;
		width: 18px;
		height: 18px;
		border: 1px solid #cccccc;
		background: #fff;
	}
	input[type=radio] + label:after{
		content: " ";
		position: absolute;
		top: 50%;
		left: 18px;
		transform: translateY(-50%);
		width: 12px;
		height: 12px;
		background: transparent;
	}
	input[type=checkbox] + label:after{
		content: " ";
		position: absolute;
		top: 50%;
		left: 13px;
		transform: translateY(-50%);
		width: 25px;
		height: 25px;
		background: transparent;
	}
	input[type=radio] + label:before,
	input[type=radio] + label:after
	{
	  border-radius:50%;
	}
	input[type=checkbox] + label:before{
	  border-radius:2px;
	}
	input[type=checkbox]:checked + label,
	input[type=radio]:checked + label{
		border-color: #09b291;
		color: #09b291;
	}
	input[type=checkbox]:checked + label:before,
	input[type=radio]:checked + label:before{
		background-color: #fff;
		border-color: #09b291;
	}
	input[type=radio]:checked + label:after{
		background-color: #09b291;
	}
	input[type=checkbox]:checked + label:after{
		background-image: url(../images/apply-checklist.png);
		background-size: contain;
		background-repeat: no-repeat;
	}
}
.apply-social{
	div{
		margin: 20px 0;
	}
	div > div{
		margin: 0;
	}
	input{
		display: inline-block;
		width: calc(100% - 60px);
	}
}
.apply-social_icon{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	margin-right: 10px;
	display: inline-block;
	color: #fff;
	font-size: 24px;
}
.apply-social_facebook{
	background-color: #4e71a8;
}
.apply-social_instagram{
	background-color: #231f20;
}
.apply-social_youtube{
	background-color: #e62f31;
}
.apply-social_twitter{
	background-color: #28aae1;
}
.apply-social_website{
	background-color: #09b291;
}
.apply-confirm{
	margin-bottom: 30px;
	p{
		font-size: 12px;
		color: #888;
		font-weight: 500;
	}
	.clearfix{
		margin: 20px 0;
		.pull-left:nth-child(1){
			width: 50px;
			height: 50px;
			margin-right: 10px;
			display: inline-block;
		}
		.apply-fill{
			width: calc(100% - 67px);
			display: inline-block;
		}
	}
}
.apply-fill{
	color: #000;
	font-size: 14px;
	font-weight: 500;
	padding: 15px 10px;
	border-bottom: 1px solid #cccccc;
}
.apply-fill_view{
	word-break: break-word;
	word-wrap: break-word;
	padding-right: 15px;
}
.apply-edit_pencil{
	width: auto;
	height: auto;
	right: 5px;
	cursor: pointer;
	font-size: 16px;
	color: #cccccc;
}
.apply-phonearea{
	width: 50px;
	display: inline-block;
}
.apply-phonenumber{
	width: calc(100% - 57px);
	display: inline-block;
}
.apply-thanks{
	top: 30%;
	left: 50%;
	transform: translate(-50%, -30%);
	div{
		font-size: 36px;
		font-weight: 600;
		color: #793da0;
		margin-bottom: 50px;
		font-family: einstellungbold;
		&:before{
			content: "";
			position: absolute;
			bottom: -25px;
			left: 50%;
			transform: translateX(-50%);
			width: 100px;
			height: 2px;
			background-color: #793da0;
			opacity: 0.25;
		}
	}
	p{
		font-size: 14px;
		font-weight: 500;
		color: #7f7f7f;
	}
	span{
		font-weight: 700;
	}
}
.apply-decorative{
	opacity: 1;
	transition: all 0.3s ease;
}
.apply-decorative_1{
	top: 0%;
	left: 40%;
	transform: translate(-40%, -0%);
	width: 90px;
	height: 60px;
	path{
		stroke-width: 0.1px;
		fill: #EFEEEE;
	}
}
img.apply-decorative_1{
	opacity: 0.1;
}
.apply-decorative_2{
	top: 8%;
	left: 35%;
	transform: translate(-35%, -8%) rotate(-90deg);
	width: 15px;
	height: 80px;
	.st0{
		stroke: none;
	}
}
@keyframes rotating {
    0%   {
    	transform: rotate(0deg);
    }
    100%  {
    	transform: rotate(720deg);
    }
}
