// -----------------------------------------------------------------------------
// This file contains styles that are specific to the feature page.
// -----------------------------------------------------------------------------
// feature tree
.feature-tree{
	width: 60px;
	top: 30%;
	left: 15%;
	bottom: 0;
	transform: translateY(150%);
	transition: all 1s ease;
	&:before{
		content: " ";
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		width: 1px;
		height: 100%;
		background-color: #09b291;
	}
	&.active{
		transform: translateY(0);
	}
}
.feature-tree_root{
	top: 0;
	left: 50%;
	transform: translate(-50%, 0) scale(0.2);
	transform-origin: top;
	width: 60px;
	height: 60px;
	margin: auto;
	border: 10px solid #09b291;
	background-color: #37d0b2;
	border-radius: 50%;
	transition: all 0.3s ease;
	cursor: pointer;
	transition-delay: 0.3s;
	&:nth-child(3){
		top: 125px;
		&.active{
			top: 100px;
		}
	}
	&:nth-child(4){
		top: 250px;
		&.active{
			top: 225px;
		}
	}
	img, svg{
		width: 25px;
		height: 25px;
		opacity: 0;
		transition-delay: 0.3s;
		.st0{
			stroke: #fff;
			fill: #fff;
			stroke-width: 2px;
		}
		.st1{
			fill: #fff;
		}
		.st2{
			fill: #fff;
			stroke: #fff;
			stroke-width: 1px;
		}
	}
	&.active{
		top: -25px;
		transform: translate(-50%, 0) scale(1);
		border-width: 0px;
		transition-delay: 0.3s;
		img, svg{
			opacity: 1;
			transition-delay: 0.3s;
		}
	}
}
.feature-tree_dummy{
	top: 0;
	width: 13px;
	height: 13px;
	margin: auto;
	border: 2px solid #09b291;
	background-color: #37d0b2;
	margin-bottom: 100px;
	border-radius: 50%;
	transition: all 0.6s ease;
	cursor: pointer;
	margin-bottom: 0;
}

// feature bigtext
.feature-bigtext{
	width: 400px;
	padding: 15px 0;
	top: 40%;
	left: 75%;
	transform: translate(-75%, -40%);
    p{
    	font-size: 90px;
		line-height: 85px;
		font-weight: 700;
		font-family: einstellungbold;
		letter-spacing: -4px;
    	color: #ed2929;
		background: linear-gradient(to bottom, #ec2929, #c52222);
	    -webkit-background-clip: text;
	    -webkit-text-fill-color: transparent;
	    transition: all 0.3s ease;
	    margin: 0;
	    opacity: 0;
	    padding-bottom: 10px;
    }
	&.active{
		p{
			opacity: 1;
			transition-delay: 0.3s;
		}
	}
}
.ie-fallback{
	p{
		background: none;
	}
}

// feature animation
.feature-animation{
	top: 50%;
	left: 10%;
	transform: translateY(-50%);
	width: 100%;
	max-width: 500px;
	display: inline-block;
	&:before{
		content: "";
		display: block;
		position: relative;
		padding-top: 85%;
	}
	&.active{
		.feature-animation_pattern{
			transform: scale(1);
			transition-delay: 0.3s;
		}
		// animation 1
		.feature-expert{
			.w-half:nth-child(1){
				img{
					opacity: 1;
					transition-delay: 1s;
					transform: translateY(60%);
				}
			}
			.w-half:nth-child(2){
				img{
					opacity: 1;
					transition-delay: 0.6s;
					transform: translateY(0);
				}
				img + img{
					opacity: 1;
					transition-delay: 1.4s;
					transform: translateY(0);
				}
			}
		}
		.feature-animation_icon-1{
			transform: scale(1);
			transition-delay: 2.4s;
		}
		.feature-animation_icon-2{
			opacity: 1;
			transform: translateY(0);
			transition-delay: 1.9s;
		}
		.feature-animation_icon-3{
			opacity: 1;
			transform: translateX(0);
			transition-delay: 1.9s;
		}
		.feature-animation_icon-4{
			transform: scale(1);
			transition-delay: 2.6s;
		}
		.feature-animation_icon-5{
			transform: scale(1);
			transition-delay: 2.8s;
		}
		.feature-animation_icon-6{
			opacity: 1;
			transform: translateY(0);
			transition-delay: 1.9s;
		}
		.feature-animation_icon-7{
			transform: scale(1);
			transition-delay: 3s;
		}
		.feature-animation_icon-8{
			opacity: 1;
			transform: translateX(0) rotate(-90deg);
			transition-delay: 0.9s;
		}
		.feature-animation_icon-9{
			opacity: 0.1;
			transform: translateY(0);
			transition-delay: 0.9s;
		}
		.feature-animation_icon-10{
			opacity: 0.3;
			transform: translateX(0);
			transition-delay: 0.9s;
		}
		.feature-animation_icon-11{
			opacity: 1;
			transform: translateY(0);
			transition-delay: 0.8s;
		}
		.feature-animation_icon-12{
			transform: scale(1);
			transition-delay: 1s;
		}
		.feature-animation_icon-13{
			opacity: 1;
			transform: translateX(0);
			transition-delay: 1.1s;
		}
		.feature-animation_icon-14{
			transform: scale(1);
			transition-delay: 1.3s;
		}
		.feature-animation_icon-15{
			transform: scale(1);
			transition-delay: 1.6s;
		}
		.feature-animation_icon-16{
			transform: scale(1);
			transition-delay: 1.9s;
		}
		.feature-chat_bubble{
			p{
				transform: none;
				opacity: 0.8;
				transition-delay: 2s;
			}
			&:nth-child(1){
				transform: translateX(45px);
				p{
					transform: none;
					transition-delay: 1.5s;
				}
				img, svg{
					transform: scale(1);
					transition-delay: 1.2s;
				}
			}
			&:nth-child(2){
				p{
					transform: none;
					transition-delay: 2.3s;
				}
				img, svg{
					transform: scale(1);
					transition-delay: 2s;
				}
			}
		}
		.feature-card{
			&:nth-child(3){
				img{
					opacity: 1;
					transform: translateY(0);
					transition-delay: 2.8s;
				}
			}
			&:nth-child(4){ 
				img{
					opacity: 1;
					transform: translateY(0);
					transition-delay: 3s;
				}
			}
		}
		.feature-book{
			transform: translateY(-80%);
			transition: all 1s ease;
			transition-delay: 2.6s;
			.w-half{
				opacity: 0.5;
				transition-delay: 2.6s;
				&:nth-child(1){
					img{
						opacity: 1;
						transform: translateY(0);
						transition: all 0.8s ease;
						transition-delay: 0.6s;
					}
				}
				&:nth-child(2){
					img{
						opacity: 1;
						transform: translateY(0);
						transition: all 0.8s ease;
						transition-delay: 0.9s;
					}
				}
			}
		}
		.feature-order{
			opacity: 1;
			transition-delay: 3.1s;
		}
	}
}
.feature-animation_pattern{
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	transform: scale(0);
	transition: all 0.3s ease;
}
.feature-animation_pattern-book{
	top: -25%;
	left: 15%;
	width: 90%;
	height: 100%;
}
.feature-wrap{
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	.w-half{
		padding: 0 10px;
		img{
			width: 100%;
		}
	}
}
.feature-expert{
	.w-half:nth-child(1){
		img{
			opacity: 0;
			transform: translateY(100%);
			transition: all 0.6s ease;
		}
	}
	.w-half:nth-child(2){
		img{
			opacity: 0;
			transform: translateY(100%);
			transition: all 0.6s ease;
			margin-bottom: 10px;
		}
	}
}
.feature_chat{
	transform: translateY(-45%);
	.w-half{ 
		transform: translateX(15%);
	}
}
.feature-chat_bubble{
	display: block;
	width: 100%;
	margin: 15px 0;
	div{
		display: inline-block;
	}
	img, svg{
		top: 20px;
		width: 50px;
		height: 50px;
		margin: 0 10px;
	}
	p{
		display: inline-block;
		max-width: 300px;
		padding: 10px 20px;
		font-size: 16px;
		margin: 0;
		border-radius: 10px 10px 0;
		background-color: #2eccad;
		color: #fff;
		transform: translateX(100px) scale(0);
		transition: all 0.3s ease;
	}
	&:nth-child(1){
		transform: translateX(45px);
		p{
			transform: translateX(100px) scale(0);
			border-radius: 10px 10px 0;
			background-color: #2eccad;
			color: #fff;
			transition: all 0.3s ease;
		}
		img, svg{
			transform: scale(0);
			transition: all 0.3s ease;
		}
	}
	&:nth-child(2){
		transform: translateX(-45px);
		margin: 15px 0 30px;
		p{
			transform: trasnlateX(-100px) scale(0);
			transform-origin: left;
			border-radius: 10px 10px 10px 0;
			background-color: #eeeeee;
			color: #1a1919;
			transition: all 0.3s ease;
		}
		img, svg{
			transform: scale(0);
			transition: all 0.3s ease;
		}
	}
}
.feature-book{
	transform: translateY(-40%);
	.w-half{
		opacity: 1;
		transition: all 0.8s ease;
		img{
			opacity: 0;
			transform: translateY(45%);
			transition: all 0.3s ease;
		}
	}
}
.feature-card{
	img{
		opacity: 0;
		transform: translateY(50%);
		transition: all 0.5s ease;
	}
}
.feature-order{
