// -----------------------------------------------------------------------------
// This file contains styles that are specific to the intro page.
// -----------------------------------------------------------------------------
// intro animation
@keyframes bounce {
    0%   {
    	transform: translateY(15px);
    }
    50%  {
    	transform: translateY(0);
    }
    100%  {
    	transform: translateY(15px);
    }
}
@keyframes scrolling {
    0%   {
    	opacity: 0;
		transform: translateY(-5px);
    }
    50%  {
    	opacity: 1;
		transform: translateY(0px);
    }
    100%  {
    	opacity: 0;
		transform: translateY(0px);
    }
}
// both side
.intro-bg{
	background: rgb(9,178,145); /* Old browsers */
	background: -moz-linear-gradient(top,  rgba(9,178,145,1) 0%, rgba(7,147,120,1) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  rgba(9,178,145,1) 0%,rgba(7,147,120,1) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  rgba(9,178,145,1) 0%,rgba(7,147,120,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#09b291', endColorstr='#079378',GradientType=0 ); /* IE6-9 */
	height: 100%;
	+ .intro-bg{ 
		background: rgb(176,126,205); /* Old browsers */
		background: -moz-linear-gradient(top,  rgba(176,126,205,1) 0%, rgba(129,83,156,1) 40%, rgba(97,56,125,1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top,  rgba(176,126,205,1) 0%,rgba(129,83,156,1) 40%,rgba(97,56,125,1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom,  rgba(176,126,205,1) 0%,rgba(129,83,156,1) 40%,rgba(97,56,125,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b07ecd', endColorstr='#61387d',GradientType=0 ); /* IE6-9 */
	}
}
// left side
.intro-inner{
	width: 100%;
	height: 100%;
	background-color: #fff;
	overflow: hidden;
}
.intro-attr_a{
	top: 0;
	right: 20px;
	width: 195px;
	height: 176px;
	opacity: 0.1;
}
.intro-attr_b{
	bottom: 0px;
	left: -100px;
	width: 284px; 
	height: 150px;
	opacity: 0.3;
}
.intro-logo{
	width: 100%;
	max-width: 500px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 1;
	transition: all 0.4s ease;
	.intro-logo__img{
		display: block;
		margin: 0 auto;
		&:nth-child(1){
			width: 150px;
		}
		&:nth-child(2){
			width: 250px;
		}
	}
	p{
		font-size: 32px;
		width: 80%;
		margin: 0 auto;
		margin-top: 25px;
	}
	&.fade{
		top: 40%;
		opacity: 0;
	}
	a{
		display: inline-block;
	}
}
.intro-scroll{
	bottom: 10%;
	opacity: 1;
	transition: all 0.4s ease;
	&.fade{
		bottom: 30%;
		opacity: 0;
	}
	&:before{
		content: " ";
		width: 1px;
		height: 300px;
		position: absolute;
		left: 50%;
		top: 110%;
		transform: translateX(-50%);
		border: 1px dashed #a0a0a0;
	}
	img{
		width: 40px;
		height: 40px;
		margin-bottom: 10px;
		cursor: pointer;
	}
	img + img{
		display: none;
		animation-name: bounce;
	    animation-duration: 2s;
	    animation-iteration-count: infinite;
	}
	svg{
		width: 40px;
		height: 40px;
		margin-bottom: 10px;
		cursor: pointer;
		.st0{
			stroke: none;
		}
		.st1{
			fill: #b9baba;
			display: block;
			stroke: #b9baba;
			stroke-width: 0.5px;
		}
	}
	.roller{ 
		.st1 + .st1{
			opacity: 0;
			transform: translateY(-5px);
			transform-origin: center;
			animation-name: scrolling;
			animation-duration: 2s;
			animation-iteration-count: infinite;
		}
	}
	svg + svg{
		display: none;
		animation-name: bounce;
    	animation-duration: 2s;
    	animation-iteration-count: infinite;
	}
	p{
		font-size: 10px;
	}
}
// right side
.intro-bg_r{
	bottom: 0;
	width: 100%;
	height: 250px;
	&.fallback{
		bottom: 17px;
		height: 280px;
	}
}
.intro-attr_c{
	background-position: 115% 20%;
	background-size: 280px;
	background-repeat: no-repeat;
	opacity: 0.3;
}
.intro-attr_d{
	bottom: 0;
	left: 0;
	right: -100%;
	height: 450px;
}
.intro-picture{
	bottom: 40px;
	left: -125px;
	&.active{
		.intro-1{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 0.2s;
		}
		.intro-2{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 0.4s;
		}
		.intro-3{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 0.6s;
		}
		.intro-4{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 0.8s;
		}
		.intro-5{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 1s;
		}
		.intro-6{
			opacity: 1;
			transform: rotateX(720deg);
			transition-delay: 1.2s;
		}
	}
}
.intro-picture_row{
	position: relative;
	width: 100%;
	display: block;
	white-space: nowrap;
	.w-sixqrt{
		width: 125px;
		position: relative;
		display: inline-block;
	}
	.w-sixqrt:before{
		content: " ";
		display: block;
		position: relative;
		padding-top: 100%;
	}
}
.intro-pict{
	top: 0px;
	right: 0px;
	bottom: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	opacity: 0;
	transform: rotateX(0deg);
	transition: all 0.8s ease;
}
.intro-icon{
	opacity: 0;
	transform: rotateX(0deg);
	transition: all 0.8s ease;
	background-color: #62347b;
	img, svg{
		width: 50px;
		height: 50px;
	}
}
.intro-download{
	margin-top: 20px;
}
.intro-download__img{
	width: 180px;
	margin: 0 5px;
	transition: 0.3s ease;
	&:hover{
		transform: scale(1.1);
	}
}