@font-face{
    font-family: 'einstellung';
    src:url('../fonts/NeueEinstellung-Regular.ttf') format('ttf'),
        url('../fonts/NeueEinstellung-Regular.eot') format('eot'),
        url('../fonts/NeueEinstellung-Regular.woff') format('woff'),
        url('../fonts/NeueEinstellung-Regular.woff2') format('woff2'),
        url('../fonts/NeueEinstellung-Regular.svg') format('svg'); 
}

@font-face{
    font-family: 'einstellungbold';
    src:url('../fonts/NeueEinstellung-Bold.ttf') format('ttf'),
        url('../fonts/NeueEinstellung-Bold.eot') format('eot'),
        url('../fonts/NeueEinstellung-Bold.woff') format('woff'),
        url('../fonts/NeueEinstellung-Bold.woff2') format('woff2'),
        url('../fonts/NeueEinstellung-Bold.svg') format('svg'); 
}