// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------
header{
	width: 100%;
	position: fixed;
	left: 50%;
	transform: translateX(-50%);
	padding-top: 50px;
	padding-bottom: 20px;
	background-color: transparent;
	transition: all 0.2s ease;
	.container{
		padding: 0 50px;
	}
}
.header-nonactive{
	.header-menu{
		opacity: 0;
	}
}
.header-active{
	transition-delay: 0.4s;
	background-color: #fff;
	.header-menu{
		opacity: 1;
		transition-delay: 0.4s;
		transition: all 0.2s ease;
		li{
			color: #000;
		}
		a{
			color: #000;
			border-color: #000;
		}
		.active{
			color: #2eccad;
			border-color: #2eccad;
		}
		a:hover, li:hover{
			color: #2eccad;
			border-color: #2eccad;
		}
	}
	.header-logo{
		transition-delay: 0.4s;
		transition: all 0.2s ease;
		opacity: 1;
		transform: translateX(0);
		a{
			display: inline-block;
		}
	}
}
.header-logo{
	width: 100%;
	transform: translateX(-100px);
	opacity: 0;
	transition: all 0.1s ease;
	img{
		width: 60px;
		margin-right: 3px;
		display: inline-block;
		transition: all 0.3s ease;
		vertical-align: middle;
	}
	img + img{
		width: 125px;
		margin-top: -5px;
	}
}
.header-menu{
	list-style: none;
	padding: 0;
	margin: 0;
	text-align: center;
	transition: all 0.1s ease;
	opacity: 1;
	li{
		display: inline-block;
		padding: 0 20px;
		color: #fff;
		font-size: 14px;
		line-height: 39px;
		cursor: pointer;
		font-family: einstellungbold;
		letter-spacing: 1px;
	}
	a{
		border-radius: 20px;
		border: 1px solid #fff;
		padding: 10px 20px;
		color: #fff;
		font-weight: 700;
	}
}