// -----------------------------------------------------------------------------
// This file contains styles that are specific to the notify page.
// -----------------------------------------------------------------------------
// notify content
.notify-container{
	height: auto;
	min-height: 100%;
}
.notify-bg{
	display: block;
	background-position: center bottom;
	background-repeat: repeat-x;
	background-size: 700px;
	width: 100%;
	height: 200px;
	bottom: 0;
}
.notify-wrapper{
	transform: translateY(100%);
	top: auto;
	transition-delay: 1s;
	transition: all 1s ease;
	opacity: 0;
	margin: auto;
	padding-top: 300px;
	padding-bottom: 200px;
	&.active{
		transform: translateY(0);
		opacity: 1;
	}
	a{
		display: inline-block;
		width: 100%;
		// margin-top: 50px;
	}
	img{
		display: block;
		width: 100%;
	}
}
.notify-title{
	font-size: 50px;
	font-weight: 700;
	letter-spacing: -2px;
	color: #793da0;
	font-family: einstellungbold;
	margin-bottom: 10px;
}
.notify-form{
	width: 95%;
	max-width: 800px;
	margin: auto;
	height: 450px;
	overflow: hidden;
	p{
		color: #000;
		max-width: 350px;
		font-size: 24px;
		margin: 0 auto 5px;
	}
	form{
		margin-top: 30px;
	}
	div{
		margin: 0 auto 30px;
		& > span{
			display: none;
			color: red;
			font-size: 12px;
			width: 300px;
			margin: auto;
			margin-top: 5px;
		}
	}
	input{
		width: 300px;
		text-align: center;
		margin: 0 auto;
		&::placeholder{
			font-size: 12px;
		}
	}
	.primary-button{
		width: 300px;
		margin-top: 20px;
		span{
			opacity: 0;
			position: absolute;
			right: 70px;
			top: 47%;
			transform: translate(-50px ,-50%);
			transition: all 0.2s ease;
		}
		img{
			width: 20px;
			height: 15px;
		}
	}
	.primary-button:hover, .primary-button:focus{
		span{
			opacity: 1;
			transform: translate(0, -50%);
		}
	}
	.disabled-button:hover, .disabled-button:focus{
		span{
			opacity: 0;
			transform: translate(-50px, -50%);
		}
	}
	.notify-title{
		margin: 0 auto 10px;
	}
	&.inputed{
		.notify-input{
			opacity: 0;
		}
		.notify-thanks{
			opacity: 1;
			transition-delay: 0.6s;
		}
	}
}
.notify-input{
	height: 100%;
	opacity: 1;
	transition: all 0.5s ease;
}
.notify-thanks{
	top: 50%;
	left: 50%;
	opacity: 0;
	width: 100%;
	transform: translate(-50%, -50%);
	transition: all 0.5s ease;
	.notify-title{
		margin-bottom: 45px;
	}
	.notify-title:before{
		content: "";
		position: absolute;
		bottom: -20px;
		left: 50%;
		transform: translateX(-50%);
		width: 100px;
		height: 2px;
		background-color: rgba(121,61,160, 0.25);
	}
	p{
		max-width: none;
	}
}