// media height
@media (max-height: 900px){
	// header
	.header-menu{
		li{
			font-size: 12px;
		}
	}
	// end header

	// intro section
	.intro-logo{
		.intro-logo__img{
			&:nth-child(1){
				width: 100px;
			}
			&:nth-child(2){
				width: 200px;
			}
		}
	}
	.intro-download__img{
		width: 130px;
	}
	.intro-logo p{
		font-size: 24px;
	}
	.intro-picture{
		left: -90px;
	}
	.intro-picture_row .w-sixqrt{
		width: 90px;
	}
	// end intro section

	// notify section
	.notify-wrapper{ 
		padding-top: 250px;
	}
	// end motify section

	// feature section
	.feature-animation{
		top: 55%;
	}
	// end feature section

	// apply decoration
	.apply-decorative_1{
		left: 30%;
		transform: translate(-30%, -0%);
	}
	.apply-decorative_2{
		left: 25%;
		transform: translate(-25%, -8%) rotate(-90deg);
	}
	.apply-decorative_3{
		top: 3%;
		transform: translate(-50%, -3%);
		width: 65px;
		height: 65px;
	}
	.apply-decorative_4{
		left: 75%;
		transform: translate(-75%, -15%);
	}
	.apply-decorative_5{
		left: 78%;
		transform: translate(-78%, -10%);
	}
	.apply-decorative_6{
		left: 27%;
		transform: translate(-27%, -30%);
	}
	.apply-decorative_7{
		left: 26%;
		transform: translate(-26%, -55%);
	}
	.apply-decorative_8{
		left: 70%;
		transform: translate(-70%, -55%);
	}
	.apply-decorative_9{
		left: 72%;
		transform: translate(-72%, -57%);
	}
	.apply-decorative_10{
		left: 74%;
		transform: translate(-74%, -53%);
	}
	.apply-decorative_11{
		left: 68%;
		transform: translate(-68%, -72%);
	}
	.apply-decorative_12{
		left: 68%;
		transform: translate(-68%, -66%);
	}
	// end apply decoration
}
@media (max-height: 768px){
	// intro section
	.intro-picture{
		left: -80px;
	}
	.intro-picture_row .w-sixqrt{
		width: 80px;
	}
	.intro-scroll{
		bottom: 5%;
		&:before{
			content: none;
		}
	}
	// end intro section

	// feature animation
	.feature-book{
		transform: translateY(-30%);
	}
	.feature-animation_icon-13, .feature-animation_icon-15{
		display: none;
	}
	.feature-animation_icon-16{
		top: 75%;
	}
	.feature-animation{
		&.active{
			.feature-animation_icon-16{
				transition-delay: 1.6s;
			}
		}
	}
	// end feature animation
}

