				transform: translate(-10%, -10%) rotate(-20deg);
			}
			.get-app__shape-b{
				top: 10%;
				left: 90%;
				transform: translate(-90%, -10%);
			}
			.get-app__shape-c{
				top: 30%;
				left: 77%;
				transform: translate(-77%, -30%) rotate(45deg);
			}
