				left: 25%;
				transform: translate(-25%, -5%) rotate(-15deg);
			}
		}
	}
	.get-app__phone-a, .get-app__phone-b{
		opacity: 0;
		visibility: hidden;
	}
	.get-app__phone-m{
		opacity: 1;
	}
	.get-app__download{
		top: 5%;
		left: 50%;
