		left: 50%;
		transform: translate(-50%, 0) rotate(-20deg);
	}
	.get-app__shape-b{
		top: 5%;
		left: 50%;
		transform: translate(-50%, 0);
	}
	.get-app__shape-c{
		top: 5%;
		left: 50%;
		transform: translate(-50%, 0) rotate(45deg);
	}
	.get-app__shape-d{
		top: 5%;
		left: 50%;
		transform: translate(-50%, 0);
	}
	.get-app__shape-e{
		top: 5%;
		left: 50%;
		transform: translate(-50%, 0) rotate(-5deg);
	}
	.get-app__shape-f{
