	// end footer
}
@media (max-width: 1024px){
	// header
	header{
		.w-half{
			left: 30%;
			transition: all 0.3s ease;
		}
		.w-half + .w-half{
			visibility: hidden;
			transform: translateX(100%);
			opacity: 0;
			transition: all 0.3s linear;
		}
	}
	// end header

	// intro section
	.intro_container{
		.full-height.w-half{
			width: 100%;
		}
		.full-height.w-half + .full-height.w-half{
			display: none;
		}
		.wrapper-l{
			padding: 0;
			.intro-picture{
				top: 0px;
				left: 0px;
				bottom: auto;
				width: 100%;
			}
			.w-fiveqrt{
				width: calc(100% / 5);
				position: relative;
				display: inline-block;
			}
			.w-fiveqrt:before{
				content: " ";
				display: block;
				position: relative;
				padding-top: 100%;
			}
		}
	}
	.intro-attr_a{
		display: none;
	}
	.intro-attr_b{
		display: none;
	}
	.intro-logo{
		top: 60%;
		transform: translate(-50%, -60%);
	}
	.intro-scroll{
		bottom: 2%;
		p{
			display: none;
		}
		&:before{
			content: none;
		}
		img{
			display: none;
		}
		img + img{
			display: block;
		}
		svg{
			display: none;
		}
		svg + svg{
			display: block;
		}
	}
	// end intro section

	// feature section
	.feature-tree_dummy{
		transition: none;
		display: none;
	}
	.feature-bigtext{
		left: 85%;
	}
	.feature-animation_icon-2{
		top: -10.5%;
	}
	.feature-animation_icon-9{
		top: -20%;
	}
	.feature-chat_bubble p{
		max-width: 200px;
	}
	// end feature section

	// get section
	.get-app{
		padding-top: 155px;
	}
	.get-app__wrapper{
		&.active{
			.get-app__download{
				top: 5%;
				left: 50%;
				transform: translate(-50%, 0);
			}
			.get-app__shape-a{
				top: 10%;
				left: 5%; 
				transform: translate(-5%, -10%) rotate(-20deg);
			}
			.get-app__shape-b{
				top: 10%;
				left: 97%;
				transform: translate(-97%, -10%);
			}
			.get-app__shape-c{
				top: 40%;
				left: 85%;
				transform: translate(-85%, -40%) rotate(45deg);
			}
			.get-app__shape-d{
				top: 45%;
				left: 65%;
				transform: translate(-65%, -45%);
			}
			.get-app__shape-e{
				top: 35%;
				left: 10%;
				transform: translate(-10%, -35%) rotate(-5deg);
			}
			.get-app__shape-f{
				top: 5%;
				left: 25%;
				transform: translate(-25%, -5%) rotate(-15deg);
			}
		}
	}
	.get-app__download{
		top: 5%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
	}
	// end get section

	// apply decoration
	.apply-decorative{
		opacity: 0;
		transition: all 0.3s ease;
	}
	.apply-decorative_3{
		opacity: 1;
		top: 15%;
		transform: translate(-50%, -15%);
	}
	// end apply decoration

	// apply pages
	.apply-container{
		background-image: none !important;
	}
	// end apply pages
}
@media (max-width: 900px){
	// header
	header{
		.w-half{
			left: 30%;
			transition: all 0.3s ease;
		}
		.w-half + .w-half{
			visibility: hidden;
			transform: translateX(100%);
			opacity: 0;
			transition: all 0.3s linear;
		}
	}
	// end header

	// feature section
	.feature-container{
		.full-height.w-half{
			width: 100%;
			position: relative;
			top: 20%;
			height: auto;
		}
		.full-height.w-half + .full-height.w-half{
			top: 45%;
			left: 15%;
		}
		.wrapper-l, .wrapper-r{
			padding: 0;
		}
	}
	.feature-bigtext{
		width: 80%;
		top: 25%;
		left: 15%;
		transform: translateY(-20%);
		p{
			font-size: 32px;
			line-height: 40px;
			letter-spacing: -2px;
		}
	}
	.feature-book{
		.feature-chat_bubble{
			opacity: 1;
			transition: all 0.8s ease;
		}
	}
	.feature-animation{
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		width: 70%;
		&.active{
			.feature-book{
				.w-half{
					opacity: 0;
				}
				.feature-chat_bubble{
					opacity: 0;
					transition-delay: 2.2s;
				}
			}
			.feature-animation_icon-16{
				transition-delay: 1.6s;
			}
		}
	}
	// end feature section

	// apply decoration
	.apply-decorative{
		pointer-events: none;
		opacity: 0;
		transition: all 0.3s ease;
	}
	// end apply decoration

	// footer
	footer{
		width: 90%;
	}
	// end footer
}
