		display: none;
	}
	.feature-animation_icon-16{
		width: 40px;
		height: 70px;
		top: 70%;
		left: 95%;
	}
	.feature-chat_bubble{
		margin: 0;
	}
	.feature-chat_bubble p{
		max-width: 150px;
		font-size: 12px;
	}
	// end feature section

	// notify section
	.notify-wrapper{
		padding-top: 120px;
	}
	.notify-title{
		font-size: 28px;
	}
	.notify-form{
		height: 400px;
		p{
			font-size: 14px;
			color: #4c4c4c;
		}
		.primary-button{
			margin-top: 0px;
		}
	}
	// end notify section

	// get section
	.get-app{
		padding-top: 80px;
		padding-bottom: 150px;
	}
	.get-app__wrapper{
		height: 350px;
	}
	.get-app__phone-m{
		top: 40%;
		width: 300px;
		height: 270px;
	}
	.get-app__shape-a, .get-app__shape-c, .get-app__shape-d, .get-app__shape-e, .get-app__shape-f{
		display: none;
	}
	// end get section

	// apply pages
	.apply-thanks{
		div{
			margin-bottom: 30px;
			&:before{ 
				bottom: -15px;
			}
		}
	} 
	// end apply pages

	// footer
	.copyright{
		bottom: 20px;
		transition: all 0.3s ease;
	}
	// end footer

	// footer
	footer{
		opacity: 0;
	}
	// end footer
}
