// -----------------------------------------------------------------------------
// This file contains styles that are specific to the get page.
// -----------------------------------------------------------------------------
.get-app{
	transform: translateY(100%);
	top: auto;
	transition-delay: 1s;
	transition: transform 1s ease;
	opacity: 0;
	margin: auto;
	padding-top: 120px;
	padding-bottom: 200px;
	will-change: transform;
	&.active{
		transform: translateY(0);
		opacity: 1;
	}
	a{
		display: inline-block;
		width: 100%;
	}
	img{
		display: block;
		width: 100%;
	}
}
.get-app__wrapper{
	width: 100%;
	height: 600px;
	overflow: hidden;
	background-color: #753b9a;
	background-image: linear-gradient(to top, #753b9a 0%, #8844b3 100%);
	&.active{
		.get-app__phone-a{
			transform: translate(0%, 0%);
			transition: all 1s ease;
			transition-delay: 1s;
		}
		.get-app__phone-b{
			transform: translate(0%, 0%);
			transition: all 1s ease;
			transition-delay: 1s;
		}
		.get-app__download{
			opacity: 1;
			transform: translate(-70%, -40%);
			transition: all 0.8s ease;
			transition-delay: 1.8s;
		}
		.get-app__shape-a{
			top: 15%;
			left: 72%;
			opacity: 1;
			transform: translate(-72%, -15%) rotate(-20deg);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
		.get-app__shape-b{
			top: 35%;
			left: 93%;
			opacity: 1;
			transform: translate(-93%, -35%);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
		.get-app__shape-c{
			top: 65%;
			left: 85%;
			opacity: 1;
			transform: translate(-85%, -65%) rotate(45deg);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
		.get-app__shape-d{
			top: 73%;
			left: 82%;
			opacity: 1;
			transform: translate(-82%, -73%);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
		.get-app__shape-e{
			top: 85%;
			left: 71%;
			opacity: 1;
			transform: translate(-71%, -85%) rotate(-5deg);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
		.get-app__shape-f{
			top: 83%;
			left: 63%;
			opacity: 1;
			transform: translate(-63%, -83%) rotate(-15deg);
			transition: all 0.8s ease;
			transition-delay: 1.9s;
		}
	}
}
.get-app_boundaries{
	max-width: 1800px;
	height: 100%;
	margin: auto;
}
.get-app_footer{
	opacity: 0;
	&.active{
		opacity: 1;
		transition: all 1s ease;
		transition-delay: 0.8s;
	}
}
// animation css
.get-app__phone-a{
	top: 10%;
	left: 500px;
	width: 400px;
	height: 600px;
	transform: translate(25%, 50%);
}
.get-app__phone-b{
	top: 5%;
	left: 210px;
	width: 370px;
	height: 580px;
	transform: translate(-25%, -50%);
}
.get-app__phone-m{
	opacity: 0;
	top: 45%;
	left: 50%;
	width: 600px;
	height: 550px;
	transform: translate(-50%, 100%);
}
.get-app__download{
	position: absolute;
	top: 40%;
	left: 75%;
	width: 600px;
	opacity: 0;
	transform: translate(-120%, -40%);
	h1{
		color: #fff;
	}
}
.get-app__buttonwrap{
	a{
		display: inline-block;
		width: 200px;
		margin: 0 5px;
	}
}
.get-app__button{
	transition: 0.3s ease;
	&:hover{
		transform: scale(1.1);
	}
}
.get-app__shape-a{
	width: 60px;
	height: 55px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%) rotate(-20deg);
}
.get-app__shape-b{
	width: 105px;
	height: 105px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%);
}
.get-app__shape-c{
	width: 40px;
	height: 40px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%) rotate(45deg);
}
.get-app__shape-d{
	width: 30px;
	height: 30px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%);
}
.get-app__shape-e{
	width: 120px;
	height: 120px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%) rotate(-5deg);
}
.get-app__shape-f{
	width: 40px;
	height: 40px;
	top: 50%;
	left: 65%;
	opacity: 0;
	transform: translate(-65%, -50%) rotate(-15deg);
}