// -----------------------------------------------------------------------------
// This file contains all styles related to the input component.
// -----------------------------------------------------------------------------
input{
	display: block;
	width: 100%;
	border: none;
	outline: none;
	border-bottom: 1px solid #8959a5;
	padding: 8px 5px;
	font-size: 14px;
	&::placeholder{
		color: #847f7f;
	}
}
textarea{
	box-sizing: border-box;
	display: block;
	width: 100%;
	border: none;
	outline: none;
	border-bottom: 1px solid #8959a5;
	padding: 8px 5px;
	font-size: 14px;
	resize: none;
	overflow: hidden;
	&::placeholder{
		color: #847f7f;
	}
}